<template>
  <div class="flex flex-full flex-center flex-dir-col">
    <div v-if="justError" class="flex flex-full flex-center flex-dir-col">
      <p class="title">התחרשה שגיאה</p>
      <p class="title content-title">יש לבדוק שנבחרו מסכת ודף תקינים</p>
    </div>

    <div
      v-if="offline && !justError"
      class="flex flex-full flex-center flex-dir-col"
    >
      <p class="title title-main">
        שים לב! הגירסא שברשותך הינה גירסת הדגמה בלבד.
      </p>
      <p class="title content-title">
        בגירסא זו ניתן להתרשם רק מהעמוד הראשון שבכל מסכת
      </p>
      <p class="title content">
        באפשרותך לעבור לעמוד הראשון או לרכוש גירסא מלאה
      </p>
      <div class="flex">
        <div style="margin-left:10px">
          <BaseButton @click="gofirstpage">מעבר לעמוד הראשון</BaseButton>
        </div>
        <div>
          <BaseButton @click="$emit('buymef')">רכישת גירסא מלאה</BaseButton>
        </div>
      </div>
    </div>
    <div v-else-if="!justError" class="flex flex-full flex-center flex-dir-col">
      <p class="title title-main">
        <span>זוהי גירסת הדגמה המוגבלת לעמוד הראשון שבכל מסכת</span>
        <!-- <span>שים לב! אינך מחובר למערכת</span> -->
      </p>
      <p class="title content-title">
        אורחים ומשתמשים ללא רישיון בתוקף יכולים לצפות בעמוד הראשון של כל מסכת
      </p>
      <p class="title content" v-if="user.name == null">
        באפשרותך לעבור לעמוד הראשון או להתחבר ולרכוש מנוי (במידה ולא קיים כבר
        מנוי פעיל)
      </p>
      <p class="title content" v-else>
        באפשרותך לעבור לעמוד הראשון או לרכוש מנוי
      </p>
      <div class="flex">
        <div style="margin-left:10px">
          <BaseButton @click="gofirstpage">מעבר לעמוד הראשון</BaseButton>
        </div>
        <div>
          <BaseButton @click="gotoCreateUser" v-if="user.name == null"
            >ליצירת חשבון</BaseButton
          >
          <BaseButton @click="gotoStore" v-else>לרכישת מנוי</BaseButton>
        </div>
      </div>

      <p class="title" style="margin-top:32px">
        <span>ברשותך דיסק אוצר החכמה?</span>
      </p>
      <p class="title content-title">
        באפשרותך לרכוש ולהוסיף את מפרשי האוצר לדיסק הקשיח
      </p>

      <BaseButton @click="downloadApp">להוספה לחץ כאן</BaseButton>
    </div>
  </div>
</template>

<script>
import config from "@/config/config";
import { mapState } from "vuex";

export default {
  props: ["error", "access"],
  computed: {
    ...mapState({ user: (state) => state.user }),
    offline() {
      return config.offline;
    },
    justError() {
      return this.error && this.access ? true : false;
    },
  },
  methods: {
    gofirstpage() {
      this.$emit("gofirstpage");
    },
    gotoCreateUser() {
      window.open("https://www.otzar.org/wotzar/register.aspx?src=mef");
    },
    gotoStore() {
      window.open("https://www.otzar.org/wotzar/pcionlinestore.aspx");
    },
    downloadApp() {
      window.open("https://mefo.otzar.org/setup.exe");
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  font-family: "heebo";
  letter-spacing: 0px;
  font-size: 15px;
  font-weight: bold;
  color: #b48c4f;
  margin-bottom: 15px;
}
.title-main {
  margin-bottom: 23px;
}
.content-title {
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: #6d5940;
}
.content {
  height: 15px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-top: -8px;
  color: #6d5940;
}
</style>
