<script>
import OtzarTabs from "@/components/navbarTabs/OtzarTabs";
import MainPageRouteGetter from "@/mixing/mainPageAddr";
import saveTabs from "./services/saveTabs";
import Print from "@/components/PrintData/Print.vue";
import MinimizeBar from "@/components/MinimizeBar/Container.vue";
import { mapState } from "vuex";
export default {
  components: {
    OtzarTabs,
    MinimizeBar,
    Print,
  },
  data() {
    return {
      combination: "",
      showBook: false,
      book: null,
      show: false,
      price: true,
    };
  },

  created() {
    window.addEventListener("beforeunload", (event) => {
      saveTabs();
      // Cancel the event as stated by the standard.
      // event.preventDefault();
      // Chrome requires returnValue to be set.
      // event.returnValue = "";
    });
  },
  mixins: [MainPageRouteGetter],
  computed: {
    ...mapState(["selectedTab"]),

    isSelected() {},
  },
  mounted() {
    this.$el.focus();
  },
  methods: {
    showTabs() {
      saveTabs();
    },
    keyDownDetector({ key }) {
      //console.log(this.combination);
      if (key == "ArrowLeft") {
        this.$store.state.keyPress = "MAIN-LEFT";
      } else if (key == "ArrowRight") this.$store.state.keyPress = "MAIN-RIGHT";
      if (key == 2) {
        this.combination = "";
        return;
      }
      if (this.combination.length >= 4) this.combination = "";
      this.combination += key;

      if (this.combination == "8887") {
        this.$store.state.workEdition = true;
        localStorage.setItem("worker", 1);
      } else if (this.combination == "8886")
        this.$store.state.workEdition = false;
      localStorage.removeItem("worker");
    },
  },
};
</script>

